const app = getApp()
import QSPopup from '../../components/QS-popup/QS-popup.vue';
import parser from "@/components/jyf-Parser/index";
import QQMapWX from '../../static/js/qqmap-wx-jssdk.min.js'
export default {
	components:{
		QSPopup,
		parser
	},
	data () {
		return {
			imgUrl:this.$imgUrl,
			globalData: {},
			// 页面id
			id:'',
			// 活动详情
			activeInfo:'',
			newHtml:"",
			// 用户选择
			 anchorIndex:1,
			 perIndex:1,
			//活动详情

			enrollList: [],

			priceList: [
			],
			userId: ''
		}
	},
	
	onLoad(option){
		// 加载数据
		this.globalData=app.globalData;
		this.userId = this.globalData.userInfo.id
			var targetId = option.targetId;
			var that = this;
			that.id =targetId
			var pageNum=1;
			var pageSize=1;
			if(pageNum<=pageSize){
				pageSize++
			}
			if (!app.globalData.userInfo) {
				uni.showModal({
					title: '提示',
					content: '您尚未注册，是否马上去注册页面？',
					success: function(sm) {
						if (sm.confirm) {
							uni.redirectTo({
								url: '/pages/application/member',
							})
						} else {
							uni.switchTab({
								url: '/pages/index/index'
							})
						}
					}
				})
			}
			uni.request({
				url:"https://wx.zzsnqyjxh.org/test/app/huodong/findById",
				data:{
					id:targetId,
					appUserId:1
					// appUserId:this.globalData.userInfo.id
				},
				method:"GET",
				success(res) {	
					that.activeInfo=res.data.data;
					
				},
				
			})
	},
	methods: {
		getCity(){
			var that = this;
			var newId = that.id
			// console.log(newId)
			uni.authorize({
				// 获取授权
				scope:"scope.userLocation",
				success() {
					// 引入腾讯地图api
					let qqmapsdk = new QQMapWX({
					    key: 'VQHBZ-RTP3U-UA5VC-4WWBD-D6ZH6-C7FTO'
					});
					uni.getLocation({
						type:'gcj02',
						success(res) {
							// console.log('当前位置的经度：' + res.longitude);
							// console.log('当前位置的纬度：' + res.latitude);
							// 当前位置的经度
							var longitude = res.longitude
							// 当前位置的纬度
							var latitude = res.latitude
							uni.request({
								url:"https://wx.zzsnqyjxh.org/test/app/huodong/signIn?huodongId="+newId+
								"&appUserId="+this.userId+"&longitude="+longitude+"&latitude="+latitude,
								method:"POST",
								success(res) {
									// console.log(res.data.msg)
									uni.showToast({
										icon:"none",
										title:res.data.msg,
										duration:1500
									})
									
								}
								
							})
							 // qqmapsdk.reverseGeocoder({
								//  location:{
								// 	  latitude:res.latitude,
								// 	  longitude:res.longitude
								//  },
								//  success(res){
								// 	 console.log(res)
	   							// 	 console.log(res.result.address)
								// 	 context.state.city= res.result.address
								// 	 console.log(context.state.city)
								//  }
							 // })
					
						}
					});				
				},
				fail(res) {
					uni.showToast({
						icon:"none",
						title:"如果不开启定位授权,部分功能无法实现",
						duration:2000
					})
				}
			
			})	
		},
		//点击按钮变
		// btnClick(e){
		// 	if(e.target.dataset.index != 2 ){
		// 	    this.anchorIndex = 1;
		// 	}
		// 	// }else{
		// 	//     //获取当前点击事件的下标
		// 	//     this.anchorIndex =  e.target.dataset.index
		// 	// 	uni.navigateTo({
		// 	// 		url: '/pages/enroll/engage'
		// 	// 	})
		// 	// }
		// },
		// 点击签到
		perClick(e) {
			if(e.target.dataset.index != 2){
				this.perIndex = 1;
				// 点击签到发起请求
				this.getCity()
		// 点击报名
			} else {
				this.perIndex = e.target.dataset.index
				// 点击发送报名请求
				var that =this;
				var id= that.id
				// console.log(id)
				uni.request({
					url:"https://wx.zzsnqyjxh.org/test/app/huodong/signUp?huodongId="+id+"&appUserId="+this.userId,
					method:"POST",
					success(res) {
						uni.showToast({
							icon:"none",
							title:res.data.msg,
							duration:1500
						})
						console.log(res)
						if(res.data.code !==500){
							// 支付弹出层
							that.show('QSPopup2')
						}
					}
				})
			}
		},
		//跳转互动页面
		interact() {
			uni.navigateTo({
				url: '/pages/myhome/interact'
			})
		},
		// 支付弹出层
		show(ref) {
			this.$refs[ref].show();
		},
	}
}
